<template>
  <div class="dlg-wrap">
    <div id="dlg">
      <div id="dlg_btn_close" v-on:click="hide">×</div>
      
      <figure class="dlg-img">
        <img :src="'/' + getImg(data.image)" :alt="data.merch_name">
      </figure>
      
      <p class="merch-name dlg-merch-name">{{ data.merch_name }}</p>
      <button class="desc-btn" type="button" v-on:click="descShow">desc</button>
      <form action="/" @submit.prevent="send">
        <div>
          <InputNumber v-model="amount" inputId="minmax-buttons" mode="decimal" showButtons :min="1" :max="20" fluid/>
        </div>
        <button class="submit-btn" type="submit">カートに入れる</button>
      </form>
    </div>
    <div class="desc-wrap">
      <div id="desc-dlg">
        <div id="desc_btn_close" v-on:click="descHide">×</div>
        <DescField :data="this.data" />
      </div>
    </div>
  </div>
</template>

<script>
import InputNumber from 'primevue/inputnumber';
import DescField from './DescField.vue';

export default {
  name: 'DialogField',
  props: {
    data : Object
  },
  components: {
    InputNumber, 
    DescField
  },
  data() {
    return {
      amount: 1
    }
  },
  methods: {
    getImg(img) {
      if(img == null) {
        return 'assets/img/noimage.png'
      }
      return img;
    },
    show() {
      this.checkImage();
      this.amount = 1;
      this.$el.classList.add('active');
    },
    
    hide() {
      this.$el.classList.remove('active');
    },
    
    send() {
      const sendData = {
        amount: this.amount,
        item: {
          merch_name: this.data.merch_name,
          merch_id: this.data.merch_id,
          price: this.data.price,
          image: this.data.image
        }
      };
      this.$emit('emitSetData', sendData);
      this.hide();
    },
    
    descShow() {
      this.$el.querySelector('.desc-wrap').classList.add('active');
    },
    
    descHide() {
      this.$el.querySelector('.desc-wrap').classList.remove('active');
    },
    
    checkImage() {
      const img = document.createElement('img');
      // ただのゴリ押し
      // エラーログ出ない処理あったら教えてください
      setTimeout(() => {
        img.src = '/' + this.data.image;
        img.onload = () => { this.$el.querySelector('.desc-btn').classList.remove('no-img'); };
        img.onerror = () => { this.$el.querySelector('.desc-btn').classList.add('no-img'); };
      }, 50);
    }
  }
}
</script>

