         * そもそもこの関数内でしかcartを使ってないので消して良さそう
         * @param {*} item 
         */
        searchInData(item) {
            this.cart.forEach(dat => {
                if (dat.merch_id === item.merch_id) {
                    return dat;
                }
            });
        },
        
        /**
        * 伝票番号を取得する処理
        */
        getSlip() {
            let slip;
            return slip;
        },
        
        send(data) {
            this.data = data;
            this.$refs.dialog.show();
        },
        
        emitSetData(data) {
            this.$emit('addData', data);
        },
        
        resetCart() {
            this.$emit('resetCart');
        }
    },
    emits: [
    'emitSetData',
    'addData',
    'resetCart',
    'setSlipId'
    ]
}
</script>

